html,
body {
  font-family: "Crimson Text Roman";
}

// Global styles

.container {
  @include container();
  @include sm(max-width, rem(510));
  @include md(max-width, rem(690));
  @include lg(max-width, rem(930));
  @include xl(max-width, rem(1110));
  @include xs-block() {
    padding-left: $fields;
    padding-right: $fields;
  }
  @include sm-block() {
    padding-left: $fields * 2;
    padding-right: $fields * 2;
  }
}

// Tools

.divider {
  display: inline-block;
  height: 26px;
  margin: 0 auto;
  overflow-x: hidden;
}

#section__headline {
  align-self: center;
  box-sizing: content-box;
  display: inline-block;
  vertical-align: top;
  text-align: center;
}

