.icon {
  &__awards {
    display: inline-block;
    width: 38px;
    height: 41px;
    background: url("../img/aboutUs/icon-award.png");
  }

  &__jobs {
    display: inline-block;
    width: 41px;
    height: 40px;
    background: url("../img/aboutUs/icon-checked.png");
  }

  &__happy {
    display: inline-block;
    width: 33px;
    height: 39px;
    background: url("../img/aboutUs/icon-dima.png");
  }

  &__reviews {
    display: inline-block;
    width: 41px;
